@charset "UTF-8";
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(51, 51, 51, 0.05);
  text-size-adjust: 100%; }

body {
  background-color: white;
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Hiragino Sans GB", "Microsoft Yahei", "微软雅黑", Arial, Helvetica, STHeiti, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  line-height: 1.5;
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 24px 0 0; }

p,
blockquote,
table,
hr,
dl,
ul,
ol,
pre,
address,
figure {
  margin: 12px 0 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600; }

h1 {
  font-size: 32px; }

h2 {
  font-size: 26px; }

h3 {
  font-size: 22px; }

h4 {
  font-size: 20px; }

h5 {
  font-size: 18px; }

h6 {
  font-size: 16px; }

a {
  color: #2680d9;
  text-decoration: none;
  -webkit-text-decoration-skip: objects; }
  a:active, a:hover {
    text-decoration: underline; }

b,
strong,
dt {
  font-weight: 600; }

code,
kbd,
samp {
  background-color: rgba(51, 51, 51, 0.05);
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 85%;
  padding: 0.2em 0.3em; }

pre {
  background-color: rgba(51, 51, 51, 0.05);
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 13px;
  line-height: 1.2;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding: 12px; }
  pre code {
    background-color: transparent;
    font-size: 13px;
    padding: 0; }

blockquote {
  border-left: 5px solid #ddd;
  color: #777;
  padding-left: 12px; }

ul,
ol {
  padding-left: 24px; }

dt,
dd,
ol ol,
ul ul,
ol ul,
ul ol {
  margin: 0; }

hr {
  border: 0;
  border-top: 1px solid #ddd; }

small {
  font-size: 85%; }

sub,
sup {
  font-size: 85%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.2em; }

sup {
  top: -0.4em; }

address,
time {
  font-style: normal; }

mark {
  background-color: #f0a742;
  color: #333;
  padding: 0 0.2em; }

rt {
  font-size: 60%; }

abbr[title] {
  border-bottom: 0;
  text-decoration: underline;
  text-decoration: underline dotted; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  max-width: 100%;
  vertical-align: middle; }

audio,
video {
  max-width: 100%; }

figcaption {
  color: #777;
  font-size: 85%; }

[role="button"] {
  cursor: pointer; }

a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  outline: 0;
  touch-action: manipulation; }

button,
input,
select,
textarea {
  font: inherit; }

button,
html [type=button],
[type=reset],
[type=submit],
::-webkit-file-upload-button {
  appearance: button; }

[type=search] {
  appearance: none; }

::-webkit-file-upload-button {
  font: inherit; }

[hidden] {
  display: none; }

fieldset {
  border: 1px solid #ddd;
  margin: 12px 0 0;
  padding: 0 12px 12px; }

legend {
  padding: 0 0.2em; }

optgroup {
  color: #777;
  font-style: normal;
  font-weight: normal; }

option {
  color: #333; }

progress {
  max-width: 100%; }
